<template>
  <div class="moreScroll">
    <van-steps v-if="$route.query.type == 'order'"
               direction="vertical"
               :active="0">
      <van-step v-for="item in newList"
                :key="item.id">
        <div v-if="newShow == true">
          <div class='infoBox'>
            <div class="title"
                 :style="{color:(item.status==0?'red':'black')}">{{ orderTypeObj[item.orderOperType] }}
              <span v-if="item.status == 0">失败<br />{{ item.errorMsg }}</span>
              <span v-if="item.status == 1 && item.orderOperType == 9">{{ item.preRoomSn }}→{{ item.roomSn }}</span>
            </div>
            <div v-if="item.realName || item.nickName">操作人：{{ item.realName || item.nickName }}</div>
            <div>{{ item.operTime }}</div>
          </div>
        </div>
      </van-step>
    </van-steps>
    <van-steps v-else-if="$route.query.type == 'apply' && BranchInfo.length > 0 || commitInfo.applyUser || commitInfo.createTime"
               direction="vertical"
               :active="0">
      <van-step v-for="(item,i) in BranchInfo"
                :key="i">
        <div class='infoBox'
             v-if="item.status == 0">
          <div class="title">驳回</div>
          <div style="color:#000;">驳回原因:{{ item.auditReason }}</div>
          <div>操作人：{{ item.auditUser.realName || item.auditUser.nickName || '暂无' }}</div>
          <div>{{ item.auditTime || '暂无' }}</div>
        </div>
        <div class='infoBox'
             v-if="item.status == 1">
          <div class="title">提交</div>
          <div>操作人：{{ item.auditUser.realName || item.auditUser.nickName || '暂无' }}</div>
          <div>{{ item.auditTime || '暂无' }}</div>
        </div>
      </van-step>
      <van-step v-if="commitInfo.applyUser || commitInfo.createTime">
        <div class='infoBox'>
          <div class="title">提交申请</div>
          <div>操作人：{{ commitInfo.applyUser || '暂无' }}</div>
          <div>{{ commitInfo.createTime }}</div>
        </div>
      </van-step>
    </van-steps>
    <div v-else>暂无记录</div>
  </div>
</template>

<script>
import { GetSystemDictDataByType } from '@/api/api_query';
import { GetBusinessAuditApprovalBranch } from '@/api/api_equip';
import { GetBusinessOrderOperLogByorderroom, GetBusinessRoomchangeRecord } from '@/api/api_business_hotel_and_scale';
import dayjs from 'dayjs';
export default {
    data() {
        return {
            data: [],
            orderDetail: {},
            changeRoom: [],
            roomOut: null,
            newShow: false,
            newList: [],
            BranchInfo: [],
            orderTypeObj: {},
            commitInfo: {}
        };
    },
    created() {
        this.$store.commit('setHeader', '操作记录');
        if (this.$route.query.type == 'order') {
            this.orderDetail = JSON.parse(localStorage.getItem('moreInfo'));
            let currentTime = dayjs('2020-08-06 00:00:00').unix();
            let orderTime = dayjs(this.orderDetail.order.orderTime).unix();
            if (orderTime > currentTime) {
                this.newShow = true;
                this.getInfo(this.orderDetail.id);
            } else {
                if (!this.orderDetail.handler && this.orderDetail.handlerId != 0) {
                    this.roomOut = '用户';
                } else if (this.orderDetail.handlerId == 0) {
                    this.roomOut = '系统退房';
                } else if (this.orderDetail.handler) {
                    this.roomOut = this.orderDetail.handler.realName;
                }
                this.getRecord();
            }
        } else if (this.$route.query.type == 'apply' && JSON.parse(localStorage.getItem('auditApprovalInfo'))) {
            this.commitInfo = JSON.parse(localStorage.getItem('auditApprovalInfo'));
            this.getBranch(this.commitInfo.auditApprovalId);
        }
    },
    methods: {
        getBranch(id) {
            GetBusinessAuditApprovalBranch({ auditApprovalId: id }).done((res) => {
                if (res.data.length > 0) {
                    this.BranchInfo = res.data.reverse();
                }
            });
        },
        getInfo(id) {
            GetBusinessOrderOperLogByorderroom(id).done((res) => {
                this.newList = res.data.reverse();
                this.getType();
            });
        },
        // 数据查询
        getType() {
            GetSystemDictDataByType({ dictType: 'business_order_oper_type' }).done((res) => {
                this.orderTypeObj = {};
                res.data.map((item) => {
                    this.orderTypeObj[item.dictValue] = item.dictLabel;
                });
            });
        },
        // 换房记录
        getRecord() {
            GetBusinessRoomchangeRecord(this.orderDetail.id).done((res) => {
                this.changeRoom = res.data.reverse();
            });
        }
    }
};
</script>

<style scoped>
.moreScroll {
    background-color: #f6f6f6;
    font-size: 15px;
    padding: 20px 0;
    position: fixed;
    top: 50px;
    bottom: 0px;
    left: 0px;
    right: 0px;
    overflow-y: scroll;
}
.infoBox {
    padding: 0 0 0.5rem 0.5rem;
    text-align: left;
    color: #b2b2b2;
}
.title {
    font-size: 0.9rem;
    color: #000;
    margin-bottom: 0.6rem;
}
/deep/ .van-step__circle {
    width: 10px;
    height: 10px;
}
/deep/ .van-steps--vertical {
    padding: 1rem 0 0 3rem;
}
/deep/ .van-step__icon--active,
.van-step__title--active {
    color: #f60;
}
.box {
    text-align: left;
    display: flex;
    justify-content: space-around;
    margin-top: 10px;
    color: #979797;
}
.box_left {
    display: flex;
    flex-direction: column;
}
.left_top {
    border-radius: 10px;
    background-color: #f60;
    width: 10px;
    height: 10px;
    text-align: center;
    margin-bottom: 2px;
}
.left_bottom {
    height: 24px;
    width: 1px;
    background-color: #f60;
    margin-left: 5px;
}
.box_right {
    width: 80%;
    font-size: 14px;
    line-height: 25px;
}
.wid140 {
    width: 100%;
}
.font {
    color: #000;
}
</style>



